body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a{
  text-decoration: none;
}

/* Clases para todos los componentes */

.section-title{
  padding: 0.6em 0 0 0;
  margin-top: 1em;
  margin-left: 1em;
}

.tab-content{
  text-align: center;
  padding: 1em;
  box-sizing: border-box;
}

input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px white inset;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

td, th{
  text-align: center !important;
}
.navbar-distribution{
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.left-items, .profile-circled{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.profile-circled{
    margin-right: 2rem;
}

.chip-color{
    color:white !important; 
    background-color:rgb(21, 160, 245) !important;
}

.chip-color:hover{
    background-color: rgb(58, 179, 255) !important;
}

.paper-profile{
    padding: 1rem;
    box-sizing: border-box;
    width: 80%;
    margin: 1rem auto;
}

.profile-picture{
    margin: 0 auto;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    color: transparent;
    background-size: cover;
    background-position: center;
    background-blend-mode: multiply;
    vertical-align: middle;
    text-align: center;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

.profile-picture:hover{
    background-color: rgba(0,0,0,.5);
    z-index: 10000;
    color: #fff;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    cursor: pointer;
}

.profile-picture span {
    display: inline-block;
    padding-top: 2.5em;
    padding-bottom: 4.5em;
}

.sales-oportunity-fields{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
}

.text-field{
    margin: 1em !important;
    min-width: 300px !important;
}

.column{
    -webkit-flex-basis: 33.33%;
            flex-basis: 33.33%;
}

.drawer-layout{
    padding: 2rem;
    text-align: center;
}

.button-container{
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    width: 100%;
    padding: 1rem 0;
    background-color: white;
}

.drawer-layout button{
    margin: 0 10px;
}

.blue-font{
    color: #005288;
    margin-top: -10px;
}

.small-font{
    font-size: 0.8rem;
}

.drawer-status{
    font-size: 0.9rem;
    color: gray;
    text-align: center;
}

.drawer-two-columns{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    text-align: left;
    margin: 1rem 0;
}

.left-column, .right-column{
    width: 50%;
    padding: 0 1rem;
}

.right-column{
    border-left: 2px solid rgba(0,0,0,0.12);
}

.file-layout{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.file-input{
    width: 50%;
    padding: 1rem;
    box-sizing: border-box;
}

.file-input p{
    margin: 0 0 0.3rem 0;
}

.general-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.success-upload{
    color:#55C400; 
    text-decoration: none;
    font-size: 0.8rem;
}

.success-upload:visited{
    color:#55C400; 
}

.input{
    min-width: 260px;
    padding: 18px;
    box-sizing: border-box;
}

.input input{
    width: 160px;
}

.progress{
    width: 100%;
}

.circle{
    width: 15px;
    height: 15px;
    border-radius: 50%;
}
.dashboard-layout{
    margin-top: 3rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.cards{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 4rem;
}

.card{
    position: relative;
    padding: 1rem;
    box-sizing: border-box;
}

.card h4{
    position: absolute;
    background-color: #10CE81;
    top: -35px;
    padding: 0.7rem;
    color: white;
    box-sizing: border-box;
}

.order-cell{
    cursor: pointer;
}

.general-info{
    margin: 2rem 0 1rem 0;
    text-align: left;
}

.canvasjs-chart-credit{
    display: none;
}

canvas{
    width: 300px !important;
    height: auto !important;
}

.employees-resume{
    margin: 3rem 0 2rem 0;
}

.employees-detail{
    padding: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}

.list{
    width: auto !important;
}

b{
    color: #10CE81;
}

@media(max-width: 1100px){
    .cards{
        display: block;
    }

    .card{
        margin: 2rem auto;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;   
    }
    
    .employees-resume{
        margin-top: 0;
    }
}

@media(max-width: 770px){
    .cards{
        display: block;
    }

    .card{
        margin: 2rem auto;
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;   
    }

    .employees-resume{
        margin-top: 0;
    }
}
.paper-signup{
    width: 50% !important;
    margin: 1rem auto !important;
    padding: 2rem 0rem;
    box-sizing: border-box;
}

.landing-image{
    position: fixed;
    background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.8)), url("https://firebasestorage.googleapis.com/v0/b/fss-react.appspot.com/o/default-assets%2Fbackground1.jpg?alt=media&token=37c884d4-da6b-4e97-8b73-9ad02f503611");
    background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.8)), url("https://firebasestorage.googleapis.com/v0/b/fss-react.appspot.com/o/default-assets%2Fbackground1.jpg?alt=media&token=37c884d4-da6b-4e97-8b73-9ad02f503611");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100vw;
    height: 100vh;
    z-index: -1;
    top: 0;
}
.grow{
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
